<template>
  <div class="collection">
    <div
      id="instruction"
      style="cursor:default;position: fixed;right: 4px;top: 150px;width: 25px;height: 80px;line-height: 20px;text-align:center;color:#F8A6AA;border: solid 1px #F8A6AA; border-radius:3px;font-size: 14px"
    >
      <span style="width: 30px">功</span>
      <span style="width: 30px">能</span>
      <span style="width: 30px">说</span>
      <span style="width: 30px">明</span>
    </div>
    <div class="hot-notice">
      <img src="../../../static/img/message.png" alt style="width: 18px;height: 18px;margin-bottom: 4px"/>
      <span>1、该查询统计表仅能查询已申报成功的数据，不包含未申报、申报失败，已申报但已作废或者已被更正的数据</span>
      <div style="text-indent: 22px;">2、该表只能从2019年的数据开始查询，跨度不超过12个月，2019年之前的数据请切换到旧版进行查询</div>
      <div style="text-indent: 22px;">3、由于数据处理的滞后性，建议您在申报成功3天之后再来查询最新的申报数据</div>
    </div>
    <div class="rg-top">
      <div class="rg-top-top">
        <span>税款所属期：</span>
        <el-date-picker type="date" placeholder="选择日期" style="width: 150px"
                        value-format="yyyy-MM-dd"></el-date-picker>
        -
        <el-date-picker type="date" placeholder="选择日期" style="width: 150px"
                        value-format="yyyy-MM-dd"></el-date-picker>
        <el-button type="primary" size="small">查询</el-button>
        <el-button type="primary" size="small">重置</el-button>
      </div>
    </div>
    <div style="margin: 0 10px;">
      <div id="con_in" class="con-div" style="overflow-y: auto; display: block;">
        <el-table :data="shenbaoLog" border style="max-width:100%">
          <el-table-column label="税款所属期">
            <template>{{ company[0]['shenbao_month'] }}</template>
          </el-table-column>
          <el-table-column prop="personNumber" label="申报人数"></el-table-column>
          <el-table-column prop="income" label="当期收入"></el-table-column>
          <el-table-column prop="taxPay" label="应补（退）税额"></el-table-column>
          <el-table-column prop="name" label="申报类型"></el-table-column>
          <el-table-column prop="address" label="申报时间">
            <template>{{ dateToString() }}</template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters(['company']),
    shenbaoLog () {
      return this.$store.state.form.shenbaoLog
    }
  },
  created () {
    this.$emit('router', this.$router.history.current.name)
  },
  mounted () {

  },
  methods: {
    inClick () {
      this.isIn = true
    },
    outClick () {
      this.isIn = false
    },
    addFormClick () {
      this.centerDialogVisible = true
    },
    submitForm (formName) {
      console.log(this.ruleForm.personStatus)
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    showNull () {
      this.$alert('本系统为教学系统，目前只提供常用操作功能噢', '信息', {
        confirmButtonText: '确定'
      })
    },
    nullClick () {
      this.showNull()
    },
    dateToString () {
      const date = new Date()
      var year = date.getFullYear()
      var month = (date.getMonth() + 1).toString()
      if (month.length === 1) {
        month = '0' + month
      }
      var dateTime = year + '-' + month
      return dateTime
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: block;
    margin: 0;
  }

  a {
    color: #42b983;
  }

  img {
    vertical-align: middle;
  }

  .collection {
    font-size: 12px;
    text-align: start;
  }

  .hot-notice {
    border: solid 1px #d2e7fb;
    background-color: #eaf4ff;
    box-sizing: border-box;
    margin: 10px 35px 0 10px;
    padding: 8px;
    line-height: 20px;
    text-align: left;
  }

  .rg-top {
    box-sizing: border-box;
    border: solid 1px #e0e0e0;
    padding: 0 10px;
    margin: 5px 35px 5px 10px;
    font-size: 16px;
    font-weight: bold;
    color: #000000;
  }

  .rg-top-top {
    height: 50px;
    overflow: hidden;
    padding: 0 10px;
    line-height: 50px;
    box-sizing: border-box;
  }

  .rg-top-bottom {
    height: 40px;
    padding: 0 10px;
    line-height: 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
  }

  .right-btn {
    padding: 8px 10px 8px 10px;
    border: 1px #5a98de solid;
    border-radius: 5px;
    font-size: 14px;
  }

  .right-btn:hover {
    background-color: rgb(64, 158, 255);
    color: white;
  }

  .con_btn1 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid #4586f5;
    border-width: 0px 0px 2px 0px;
    font-size: 15px;
    color: #4586f5;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .con_btn2 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid transparent;
    border-width: 0 0 2px 0;
    font-size: 15px;
    color: #000000;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .in-table .notice {
    display: block;
    width: 100%;
    height: 20px;
    /* display: block; */
    background-color: rgb(212, 244, 252);
  }

  .in-table-content {
    padding: 0 30px;
  }
</style>
<style>
</style>



